.dashboard {
    width : 100vw;
    max-width : 100%;
    display : grid;
    grid-template-columns : 10fr 1fr;
    position : absolute;
}
.dashboardContainer{
    border-left: 1px solid black;
    background-color : white;
    padding : 3rem 0;
}

.dashboardContainer > h2 {
    color : gray;
    text-align : center;
    padding : 1.5rem;
    margin :  auto;
}
.dashboardSummary {
    margin : 2rem 0;
}

.dashboardSummary > div {
    display : flex;
    background-color : white;
    justify-content: center;
}

.dashboardSummary > div > p{
    background-color : rgba(70,117,218,0.972);
    color : white;
    width : 100%;
    margin : 0.2rem;
    text-align : center;
    padding : 1.5rem;
    font : 300 1.3rem "Roboto";
}

.dashboardSummaryBox2 > a{
    color : black;
    text-decoration: none;
    font : 300 2rem "Roboto";
    text-align : center;
    display : flex;
    background-color : rgb(148, 171, 197);
    padding : 1.5rem;
    justify-content : center;
    align-items : center;
    width : 10vmax;
    height : 10vmax;
    margin : 2rem;
    border-radius: 100%;
    flex-direction : column;
}

.dashboardSummaryBox2 > a:first-child {
    background-color : blue;
    color : white;
}

.dashboardSummaryBox2 > a:last-child {
    background-color : rgba(51,51,51);
    color : white;
}
.doughnutChart{
    width : 30vmax;
    margin : auto;
}

@media screen and (max-width : 680px){
    .dashboard {
        margin-top : 200px;
    }
}

@media screen and (max-width: 600px) {
    .dashboard{
        grid-template-columns : 1fr;
    }
    .dashboardContainer{
        border-left : none;
    }
    .dashboardSummary > a {
        margin : 0;
    }
    .dashboardSummaryBox2 > a {
        padding : 0.5rem;
        margin : 1rem;
        font : 300 0.9rem "Roboto";
    }
}