.navbar {
  overflow: hidden;
  background-color: #f9f8f8;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9;
}

.navbar a {
  float: right;
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.navbar a:hover {
  color: rgb(183, 170, 170);
}

.navbar a.active {
  color: rgb(11, 11, 11);
}
.navbar a.active:hover {
  color: rgb(177, 172, 172);
}
.search-container{
  z-index: 10;
  position: fixed;
  margin : auto;
  left : 35%;
  margin-top: 15px;
}
.search-container input {
  padding: 6px;
  background-color: rgb(71, 67, 67);
  width: 400px;
  border-radius: none;
  color : rgb(237, 237, 241);
  transition: all 0.2s ease-in-out;
  font-size: 17px;
  border: none;
  outline: none;
}
.search-container input:hover {
  transform: scale(1.015);
}


@media screen and (max-width: 720px) {
  .navbar a,
  .search-container
  {
    width: 100%;
    display: block;
    text-align: left;
    margin: 0;
    left : 0;
    top : 22%;
  }
  .search-container input {
    border: 1px solid #ccc;
    display: block;
    width: 100%;
  }
  .just-padding {
    padding-top : 150px;
  }
  .post-filling {
    margin-top : 150px !important;
  }
}

.navbar .topic {
  font-family: "Tilt Neon", cursive !important;
  color: blue !important;
  font-weight: 900;
  pointer-events: none;
  cursor: unset;
}

.just-padding {
  padding-top : 60px;
}
  
@media screen and (max-width : 1050px) {

  .just-padding {
    padding-top : 60px;
  }
}

.avatar:hover {
  transform : scale(1.04);
  transition: all 0.1s ease-in-out;
}