.card-btn1 {
  position: absolute;
  padding: 8px;
  background-color: rgb(242, 244, 242);
  border-radius: 16px;
  bottom: 10px;
  top: 80%;
  left: 37%;
  height: 40px;
  transform: translate(-50%, -50%);
  width: 40px;
  display: none;
}
.card-btn2 {
  position: absolute;
  padding: 8px;
  background-color: rgb(229, 236, 229);
  border-radius: 16px;
  bottom: 10px;
  top: 80%;
  left: 64%;
  height: 40px;
  transform: translate(-50%, -50%);
  width: 40px;
  display: none;
}
.card-btn3 {
  position: absolute;
  padding: 8px;
  background-color: rgb(242, 244, 242);
  border-radius: 16px;
  bottom: 10px;
  top: 80%;
  left: 37%;
  height: 40px;
  transform: translate(-50%, -50%);
  width: 40px;
  display: none;
}
.card-img {
  transition: all 0.5s ease-out;
}

.card-div:hover .card-btn1 {
  display: block;
  opacity: 60%;
}

.card-div:hover .card-btn2 {
  display: block;
  opacity: 60%;
}

.card-img:hover {
  transform: translateY(-2px);
  opacity: 50%;
}
.card-div:hover {
  background-color: black;
}
.card-btn1:hover {
  opacity: 1 !important;
}
.card-btn2:hover {
  opacity: 1 !important;
}
.singlepage {
  padding-top: 100px;
}

.f-trash:hover {
	cursor: pointer;
	color : red;
	opacity: 60%;
}

@media screen and (max-width: 661px) {
  .singlepage {
    padding-top: 150px;
  }
}
@media screen and (max-width: 600px) {
  .singlepage {
    padding-top: 250px;
  }
  .single-page-card{
	width : 80%;
  }
}

.butt {
  box-shadow: inset 0px 1px 0px 0px #fce2c1;
  background: linear-gradient(to bottom, #ffc477 5%, #fb9e25 100%);
  background-color: #ffc477;
  border-radius: 11px;
  border: 2px solid #eeb44f;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  padding: 8px 18px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #cc9f52;
}
.butt:hover {
  background: linear-gradient(to bottom, #fb9e25 5%, #ffc477 100%);
  background-color: #fb9e25;
}
.butt:active {
  position: relative;
  top: 1px;
}
