.profileContainer{
    display : flex;
    height : 100vh;
    width : 100vw;
    position : fixed;
    top : 0%;
    left : 0%;
    max-width : 100%;
    background-color: white;
}

.profileContainer > div {
    height : 100vh;
    display : flex;
    width : 100vw;
    max-width : 100%;
    flex-direction: column;
    justify-content : center;
    align-items : center;
}

.profileContainer > div:first-child > h1 {
    color : rgba(252, 19, 19, 0.555);
    font : 500 2.2vmax "Roboto";
    padding : 10%;
    transform : translateX(-10vmax) translateY(-2vmax);
}

.profileContainer > div:first-child > img{
    width : 20vmax;
    border-radius : 50%;
    height : 20vmax;
    transition : all 0.5s;
}
.profileContainer > div:first-child > img:hover {
    transform : scale(1.05);
}

.profileContainer > div:first-child > a {
    border : none;
    background-color : rgb(230, 207, 173);
    font : 400 1vmax "Roboto";
    color : white;
    margin : 4vmax;
    text-align : center;
    width : 30%;
    transition : all 0.5s;
    padding : 0.5vmax;
    text-decoration: none;
}

.profileContainer > div:first-child > a:hover {
    background-color : rgb(225, 238, 243);
}

.profileContainer > div:last-child {
    justify-content: space-evenly;
    align-items : flex-start;
    padding: 5vmax;
    box-sizing : border-box;
}

.profileContainer > div:last-child > div > p{
    color : rgba(0,0,0,0.418);
    font : 400 1vmax cursive;
    margin : 0.2vmax;
}
.profileContainer > div:last-child > div > h4{
    color : black;
    font : 400 1.2vmax cursive;
}

.profileContainer > div:last-child > div:last-child {
    display : flex;
    flex-direction: column;
    width : 60%;
}

.profileContainer > div:last-child > div:last-child > a{
    border : none;
    background-color :rgb(68,68,68);
    font : 400 1vmax "Roboto";
    color : white;
    text-decoration: none;
    padding : 0.5vmax;
    transition : all 0.5s;
    margin : 1vmax 0;
    text-align : center;
}
.profileContainer > div:last-child > div:last-child > a:hover{
    background-color : rgb(31,31,31);
}
@media screen and (max-width : 600px){
    .profileContainer{
        overflow-y: scroll;
    }
    .profileContainer {
        padding-top: 50%;
        flex-direction : column;
    }
    .profileContainer > div:first-child > h1{
        font : 500 1.7vmax "Roboto";
        padding : 1vmax;
    }
    .profileContainer > div:last-child{
        text-align : center;
        align-items : center;
    }
    .profileContainer > div:last-child > div > h4 {
        font : 400 2.5vmax "Roboto";
    }
    .profileContainer > div:last-child > div > p{
        font : 400 2vmax "Roboto";
        margin : 0.5vmax 0;
    }
    .profileContainer > div:last-child > div:last-child > a{
        font : 400 1.8vmax "Roboto";
        padding : 1vmax;
        margin : 2vmax 0;
    }
}