.body {
  margin : 0;
  padding : 0;
  box-sizing: border-box;
}
.auth-form-container {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  color: white;
  background-image: linear-gradient(79deg, #dced8e, #c66fbc 48%, #f7944d);
}

.auth-form-container,
.login-form,
.register-form {
  display: flex;
  flex-direction: column;
}

.login-form, .register-form {
  padding: 10px;
  min-width: 20%;
  box-shadow: 0 0 0 0.1rem skyblue;
}

@media screen and (min-width: 600px) {
  .auth-form-container {
    padding: 5rem;
    border: 1px solid white;
    border-radius: 10px;
    margin: 0.5rem;
  }
}

label {
  text-align: left;
  padding: 0.25rem 0;
}

input {
  margin: 0.5rem 0;
  padding: 1rem;
  border: none;
  border-radius: 10px;
}

.log-btn {
  border: none;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
  color: #7439db;
}

.link-btn {
  background: none;
  color: white;
  border : none;
  text-decoration: underline;
}

@media screen and (max-width: 599px) {
  .auth-form-container{
    padding-top: 20%;
  }
}
