.popup-box {
  position: fixed;
  background: #00000050;
  width: 100vw;
  height: 100vh;
  top: 0%;
  padding-top : 9%;
  left: 0;
}
 
.box {
  position: relative;
  max-width: 40vw;
  margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #999;
  overflow: auto;
}
 
.close-icon {
  content: 'x';
  cursor: pointer;
  position: fixed;
  background: #ededed;
  width: 25px;
  left : 49%;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}
.pop-btn{
  right : 3%;
  top : 20%;
  position :fixed;
  z-index: 3000;
}

.edit-div{
  max-width : 50vw;
  margin : 0 auto;
  box-shadow: 10px 10px 5px lightblue;
  margin-top : 10%;
}

@media screen  and (max-width:624px){
  .box {
    min-width: 100vw;
    top : 30%;
  }
  .edit-div{
    margin-top : 60%;
    min-width: 100vw;
  }
}

